body {
  background-color: #282c34;
  font-family: monospace, 'Courier New', Courier;
}

.CenterContainer {
  position: absolute;
  top: 45%; width: 98%;
  transform: translate(0%, -50%);
  color: white;
}

.Welcome {
  word-spacing: -10pt;
  text-align: center;
  font-size: 50pt;
}

.WelcomeMobile {
  word-spacing: -10pt;
  text-align: center;
  font-size: 30pt;
}

.Hint {
  word-spacing: -2pt;
  text-align: center;
  font-size: 12pt;
}

.AccessCode {
  margin-top: 25px;
  font-size: 24pt;
  font-weight: 500;
  text-align: center;
}

::placeholder {
  font-family: monospace, 'Courier New', Courier;
  font-size: 11pt;
}

button {
  margin-left: 1px;
  font-family: monospace, 'Courier New', Courier;
  font-size: 11pt;
}

.AccessDenied {
  color: red;
  margin-top: 10px;
  word-spacing: -2pt;
  text-align: center;
  font-size: 12pt;
}

.NoAccessDenied {
  color: red;opacity: 0;
  margin-top: 10px;
  word-spacing: -2pt;
  text-align: center;
  font-size: 12pt;
}

.HiddenCode {
  position: absolute;
  bottom: 10px;
  left: 10px;
  font-size: 10pt;
  color: #282c34;opacity: 0.1;
}

.Version {
  font-size: 10pt;
  word-spacing: -2pt;
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: white;
}

.NoVersion {
  font-size: 10pt;
  word-spacing: -2pt;
  position: absolute;
  bottom: 10px;
  right: 10px;
  color: white;opacity: 0;
}

::selection {
  background: #fcf876; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: #fcf876; /* Gecko Browsers */
}

.PortfolioPage {
  color: white;
}

.Resume {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Section {
  margin: 10px;
  padding: 10px 10px;
  border: 2px solid #fcf876;
  background-color: #333;
  width: 70%;
}

h1{
  font-size: 36pt;
  color: #fcf876;
  margin: 0px;
}

h2 {
  font-size: 24pt;
  color: #fcf876;
  margin: 0px;
}

p {
  margin: 0px;
}

.Congrats {
  margin-bottom: 30px;
}

ul{
  margin: 3px;
}

em {
  color: #fcf876;
}

a {
  color: #fcf876;
}

.Heading {
  margin: 10px;
  padding: 10px 10px;
  border: 2px solid #fcf876;
  background-color: #333;
  width: 70%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.Projects {
  margin: 10px;
  padding: 10px 10px;
  border: 3px solid #fcf876;
  border-width: 2px;
  background-color: #333;
  width: 70%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.LambdaTable {
  border: 2px solid #fcf876;
  margin: 5px;
}